import './App.css';
import React, {useState} from 'react';
import BG1 from './assets/bg1.jpg';
import BG3 from './assets/bg3.png';
import profile from './assets/profile.jpg';
import './styles.css'

import TypeWriterEffect from 'react-typewriter-effect';
import { Parallax, ParallaxLayer} from '@react-spring/parallax';
import TinderCard from 'react-tinder-card';
import u1 from './assets/u1.jpg';
import u2 from './assets/u2.png';
import u3 from './assets/u3.png';
import u4 from './assets/u4.png';
import u5 from './assets/u5.png';




//scrolling
//containers for navigation
function App() {

   const [ind, setIndex] = useState(0);

   const UIUX = [u1, u2, u3, u4, u5];

    return (
      <div style={{height:"100vh", backgroundImage:`url(${BG1})`, backgroundSize:'100% 100%'}}>
        <Parallax pages={4}>
          <ParallaxLayer speed={2}>
            <div style={{display:'flex', height:"100vh", alignItems: 'center', justifyContent:'center', }}>
              <div style={{display: 'flex', flexDirection: 'column', width:"65%", height:'30vh', alignItems:'center'}}>
                
                <div style={{height: "70%", width:'80%', fontSize: 40, display:'flex', alignItems: 'center', color:'black'}}>
                  <h1>Hi, I am Zheng Xi</h1>
                </div>

                <div style={{height: "30%", width: "70%", display:'flex', alignItems:'center', color:'white'}}>
                  <TypeWriterEffect
                    textStyle={{color: 'white', fontWeight: 500, fontSize: '1.5em',}}
                    cursorColor="white"
                    multiText={['I code', 'I do UI/UX', 'I am an engineer']}
                    typeSpeed={40}
                  />
                </div>
                
              </div>
            </div>
          </ParallaxLayer>


          <ParallaxLayer offset={1} speed={1} style={{display:'flex', flexDirection:"row", justifyContent:"center", alignItems:"center", backgroundColor:"white"}}>
              <div style={{backgroundColor:"#987923", width:"35%"}}>
              <img style={{width:"100%", height:"100%"}} src={profile}/>
              </div>

              <div style={{height:"48%",width:"40%", }}>
                <div style={{display:'flex', justifyContent:'center'}}>
                  <h1 style={{fontSize:20}}>About Me</h1>
                </div>
                <div style={{display:'flex', justifyContent:'center', alignItems:"end"}}>
                  <p style={{width:"82%", display:"flex", justifyContent:"flex-end"}}>
                  Hi, I am Zheng Xi and I am a Computer Engineering undergraduate at National University of Singapore. I am a tea lover 🍃 that 
                  spends his free time reading 📖, building nanoblocks 🧩, cooking 🍳 and checking out dessert spots 🍨. My passion lies in the development of Artifical
                  Intelligence as well as its application in our daily lives. Scroll down to find out more about the projects I was involved in!
                  </p>
                </div>
              </div>
          </ParallaxLayer>

          <ParallaxLayer offset={2} style={{backgroundColor:"white", display:'flex', flex:1, flexDirection:'column'}}>
          <div style={{display:'flex', flex:1, justifyContent:'center', alignItems:'end'}}>
            <p style={{fontSize:20, fontWeight:'bold'}}>My Heart(hard)ware Projects</p>
          </div>

          <div style={{display:'flex', flex:6, backgroundImage:`url(${BG3})`, backgroundSize:'100% 100%'}}>
            <div style={{display:'flex', flex:1, flexDirection:'column', alignItems:'center'}}>
              <div style={{marginTop:15, fontWeight:"bold", fontSize:17}}><p>Automating navigation of maze using the mbot</p></div>
              <div style={{width:"72%", height:"48%", marginTop:20}}>
                <iframe src={`https://www.youtube.com/embed/nJr4PbxbPn8`} allowFullScreen width="100%" height="100%"/>
                </div>
              <div style={{width:"72%", marginTop:20, fontSize:13}}>
                <p>Personally, this was the most interesting part of the CG1111A module. 
                The mbot redjusts the direction it is heading based on the data received from the Ultrasonic and Infrared Sensor. 
                The LED and LDR installed at the bottom of the device assists the mbot in the detection of the color pad. 
                Keep a lookout for the celebratory music when the mbot reaches the endpoint!</p></div>
            </div>

            <div style={{display:'flex', flex:1, flexDirection:'column', alignItems:'center'}}>
              <div style={{marginTop:15, fontWeight:"bold", fontSize:17}}><p>My attempt at baremetal programming </p></div>
              <div style={{width:"72%", height:"48%", marginTop:20}}>
                <iframe src={`https://www.youtube.com/embed/JsvSBepJblw`} allowFullScreen width="100%" height="100%"/>
                </div>
              <div style={{width:"72%", marginTop:20, fontSize:13}}>
                <p>This was the second studio of the CG2111A module, where we put to practice the knowledge we obtained with regards to soldering and wiretapping.
                The logic for the LEDs were encoded using via baremetal programming. This is an early new year gift as we are able to bring the product home! 
                </p>
              </div>
            </div>
          </div>
          </ParallaxLayer>

          <ParallaxLayer offset={3} style={{backgroundColor:'white', display:'flex', flex:1, flexDirection:'column'}}>
            <div style={{display:'flex', flex:1, justifyContent:'center'}}>
              <p style={{fontSize:25, fontWeight:700}}>My UI/UX Designs </p>
            </div>

            <div style={{display:'flex', flex:7, justifyContent:'center'}}>
            <div style={{width:"30%", display:'flex', justifyContent:'center', alignItems:"center"}}>
            <div style={{height:"50%", display:'flex', flexDirection:'column', justifyContent:'center',alignItems:'center'}}>
              <p style={{fontSize:17, fontWeight:"bold", height:"1%"}}>Swipe to see the other designs!</p>
              <img style={{transform:'rotate(90deg)', width:"40%"}} src={require('./assets/arrow.png')}></img>
            </div>
            
            </div>
              <div style={{width:"25%", display:'flex'}}>
            {UIUX.map(pic => 
              <TinderCard className='swipe'>
                  <div style={{ backgroundImage: 'url(' + pic + ')'}} className='card'>
                  <h1></h1>
                  </div>
                </TinderCard>)}
              </div>
              
            </div>
          </ParallaxLayer>
          
        </Parallax>
      </div>
    );
}

export default App;
